izan la energía solar para transformar la materia inorgánica en orgánica como son los organismos autótrofos.")])])])]),_c('div',{staticClass:"col-12 col-lg-4 mb-3 mb-lg-0",attrs:{"data-aos":"flip-down"}},[_c('div',{staticClass:"bgr_10 p-4 h-100"},[_c('figure',{staticClass:"mb-3 d-flex justify-content-center"},[_c('img',{staticClass:"img120",attrs:{"src":require("@/assets/curso/temas/tema1/v2022/t1-16.svg"),"alt":""}})]),_c('div',{staticClass:"text-center"},[_c('h4',[_vm._v("Consumidores")]),_c('p',{staticClass:"mb-0"},[_vm._v("Son los que se alimentan de los productores obteniendo así la energía y materia que necesita conocidos como organismos heterótrofos y están clasificados en consumidores primarios, secundarios y terciarios según la depredación.")])])])]),_c('div',{staticClass:"col-12 col-lg-4",attrs:{"data-aos":"flip-up"}},[_c('div',{staticClass:"bgr_10 p-4 h-100"},[_c('figure',{staticClass:"mb-3 d-flex justify-content-center"},[_c('img',{staticClass:"img120",attrs:{"src":require("@/assets/curso/temas/tema1/v2022/t1-17.svg"),"alt":""}})]),_c('div',{staticClass:"text-center"},[_c('h4',[_vm._v("Descomponedores")]),_c('p',{staticClass:"mb-0"},[_vm._v("Son los que transforman los residuos orgánicos e inorgánicos en materia después de haber sido usada por los productores como son las bacterias y hongos.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row bgr_11 px-4 py-5 justify-content-center",attrs:{"data-aos":"zoom-out"}},[_c('div',{staticClass:"col-12 col-lg-9"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido"},[_c('h5',[_vm._v("Figura 3")]),_c('span',[_vm._v("Pirámide de niveles tróficos ecosistema terrestre")])]),_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/v2022/t1-18.png"),"alt":"La imagen muestra una pirámide trófica del ecosistema terrestre. En la base están los productores (plantas). Les siguen los consumidores primarios (herbívoros como ciervos y