},[_c('p',{staticClass:"mb-0"},[_vm._v("La economía colombiana está representada en el volumen de exportaciones con más de 98 toneladas y el consumo interno se evalúa en 305 millones de toneladas. Este equilibrio entre entradas y salidas se concluye con el volumen de materiales que no representa un valor dentro de la economía, como por ejemplo, los residuos originados de materiales de la minería y la biomasa no empleada. En relación a los residuos domésticos, se calcula que son desechados alrededor de 18 millones de toneladas equivalentes a un 4.5 % de la extracción doméstica. Los residuos domésticos están conformados por residuos orgánicos (59 %), otros (16 %), plásticos (13 %), papel y cartón (9 %), vidrios (2 %) y metales (1 %). En el siguiente gráfico se puede observar una estimación del metabolismo de estos materiales:")])])])}]

